@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile {
    background-color: #2C1338;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet {
        display: none;
    }

    h1 {
        color: #D164C5;
        font-size: 5rem;
        margin: 0;
    }

    .menu {
        img {
            width: 5rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}

.barra {
    display: none;
    
    @include m.tablet {
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, .3);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p {
        margin: 0;
        font-size: 1.6rem;

        span {
            font-weight: 900;
            color:#2C1338;
        }
    }

    .cerrar-sesion {
        @include m.boton(rgb(221, 111, 209), v.$blanco);
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: 0.5rem;
    }
}